.card-container {
    display: inline-block;
    padding: 1rem;
    width: 100%;
    border-radius: 18px;
    border: solid 5px white;
    background-color: white;
    box-shadow: 2px 2px 16px rgba(0, 0, 0, 0.9);
    text-align: center;
    transition: transform 0.3s;
}

.card-container:hover {
    border: solid 5px var(--clr-main);
    transform: translateY(5px);
}

.card-img img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.card-text a {
    text-decoration: none;
}

.card-name {
    cursor: pointer;
}

.card-name p {
    display: inline;
    text-transform: capitalize;
    color: var(--clr-main);
    font-size: var(--font-subtitle);
    font-weight: 600;
}

.card-name p:last-child {
    margin-left: 6px;
    text-transform: capitalize;
}

.card-position {
    color: var(--clr-text);
    font-size: var(--font-text);
    font-weight: 600;
    margin-top: 10px;
    text-transform: capitalize;
}
.card-buttons {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 1rem;
}

.edit-icon {
    width: 45px;
    height: 45px;
    border-radius: 10px;
    background-color: white;
    border: solid 2px green;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: green;
    cursor: pointer;
}

.edit-icon:hover {
    color: white;
    background-color: green;
}

.delete-icon {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: solid 2px #df362d;
    color: #df362d;
    cursor: pointer;
}

.delete-icon:hover {
    color: white;
    background-color: #df362d;
}

